const state = {
  user: {
    id: 1,
    name: 'kriangkrai',
    listMoney: [{
      totalPlayer: 20,
      numberByMoney: 2,
      money: 1000,
      listInterst: [
        { interest: 700 },
        { interest: 500 }
      ]
    }]
  }
}

const mutations = {
  addNewMoney (state, obj) {
    state.user.listMoney.push(obj)
  }
}

const actions = {
  addPayment: ({ commit }, obj) => {
    commit('addNewMoney', obj)
  }
}

const getters = {
  dataUser () {
    return state.user
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



